import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
// import { AFrameRenderer, Marker } from 'react-web-ar'
// import { Entity } from "aframe-react";

import './SceneViewer.css';

function cn(name) {
  return `components__SceneViewer__${name}`;
}

class SceneViewer extends PureComponent {
  render() {
    const { className = '' } = this.props;
    return (
      <div className={`${cn('layout')} ${className}`}>
        {/* <AFrameRenderer
          arToolKit={{
            trackingMethod: "best",
            sourceType: "image",
            sourceUrl: "./images/hiro_marker.png"
          }}
          inherent={true}
        >
          <Marker parameters={{ "hit-testing-enabled": true, preset: "hiro" }}>
            <Entity
              geometry={{ primitive: "box" }}
              material={{ color: "red" }}
              position={{ x: 0, y: 0.03, z: 0 }}
            />
          </Marker>
        </AFrameRenderer> */}
      </div>
    );
  }
}

SceneViewer.propTypes = {
  className: PropTypes.string,
};

SceneViewer.defaultProps = {
};

export default SceneViewer;



